.main {
  position: relative;
  &__wrapper {
    background-color: rgba($white, 0.2); }
  &__title {
    color: #363636;
    font-size: 2rem;
    font-weight: 60;
    line-height: 1.125; }
  &__subtitle {
    color: #4a4a4a;
    font-size: 0.75rem;
    font-weight: 400;
    line-height: 1.25; } }
.container {
  max-width: 850px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  padding: 20px 30px 40px;
  margin-bottom: 40px; }
.box {
    background-color: white;
    border-radius: 6px;
    box-shadow: 0 0.5em 1em -0.125em rgba(10, 10, 10, 0.1), 0 0px 0 1px rgba(10, 10, 10, 0.02);
    color: #4a4a4a;
    display: block;
    padding: 1.25rem;
    z-index: 9999;
    border: 1px solid rgba(10, 10, 10, 0.1), 0 0px 0 1px rgba(10, 10, 10, 0.02); }
.content {
  border-bottom: 1px solid #EBEFF1;
  margin-left: -1.25rem;
  margin-right: -1.25rem;
  padding: 0 1.25rem 20px; }
.subtitle {
  color: #4a4a4a;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.25; }
.content:not(:last-child), .subtitle:not(:last-child) {
  margin-bottom: 1.5rem; }
.field:not(:last-child) {
  margin-bottom: 0.75rem; }

.label {
  color: #363636;
  display: block;
  font-size: 1rem;
  font-weight: 700; }
.label:not(:last-child) {
  margin-bottom: 0.5em; }
.control {
  box-sizing: border-box;
  clear: both;
  font-size: 1rem;
  position: relative;
  text-align: left; }
.input {
  box-shadow: inset 0 0.0625em 0.125em rgba(10, 10, 10, 0.05);
  max-width: 100%;
  width: 100%;
  align-items: center;
  border: 1px solid transparent;
  border-radius: 4px;
  display: inline-flex;
  height: 2.5em;
  justify-content: flex-start;
  line-height: 1.5;
  padding-bottom: calc(0.5em - 1px);
  padding-left: calc(0.75em - 1px);
  padding-right: calc(0.75em - 1px);
  padding-top: calc(0.5em - 1px);
  position: relative;
  vertical-align: top;
  font-size: 0.75rem;
  background-color: white;
  border-color: #dbdbdb;
  color: #363636;
  &:focus {
    outline: none; } }
.file {
  align-items: stretch;
  display: flex;
  justify-content: flex-start;
  position: relative;
  user-select: none;
  width: 100%; }
.file.is-fullwidth .file-label {
  width: 100%; }
.file-label {
  align-items: stretch;
  display: flex;
  cursor: pointer;
  justify-content: flex-start;
  overflow: hidden;
  position: relative;
  width: 100%; }
.file-input {
  height: 100%;
  left: 0;
  opacity: 0;
  outline: none;
  position: absolute;
  top: 0;
  width: 100%; }
.file-cta {
  border-radius: 4px;
  font-size: 1.6em;
  padding-left: 1em;
  padding-right: 1em;
  white-space: nowrap;
  background-color: whitesmoke;
  color: #4a4a4a;
  align-items: center;
  border-color: #dbdbdb;
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  box-shadow: none;
  display: inline-flex;
  height: 2.5em;
  justify-content: flex-start;
  line-height: 1.5;
  position: relative;
  vertical-align: top;
  width: 30%; }
.file-name {
  flex-grow: 1;
  border-color: #dbdbdb;
  border-style: solid;
  border-width: 1px 1px 1px 0;
  display: block;
  max-width: 100%;
  overflow: hidden;
  text-align: left;
  text-overflow: ellipsis;
  border-radius: 4px;
  font-size: 1em;
  padding-left: 1em;
  padding-right: 1em;
  white-space: nowrap;
  align-items: center;
  display: inline-flex;
  font-size: 1rem;
  height: 2.5em;
  justify-content: flex-start;
  line-height: 1.5;
  position: relative;
  vertical-align: top; }
.select.is-fullwidth {
  width: 100%; }
.select.is-small {
  border-radius: 2px;
  font-size: 0.75rem; }
.select:not(.is-multiple) {
  height: 2.5em; }
.select {
  display: inline-block;
  max-width: 100%;
  position: relative;
  vertical-align: top; }
.select:not(.is-multiple):not(.is-loading)::after {
  border-color: #3273dc;
  right: 1.125em;
  z-index: {} }
.select:not(.is-multiple):not(.is-loading)::after, .navbar-link:not(.is-arrowless)::after {
  border: 3px solid transparent;
  border-radius: 2px;
  border-right: 0;
  border-top: 0;
  content: " ";
  display: block;
  height: 0.625em;
  margin-top: -0.4375em;
  pointer-events: none;
  position: absolute;
  top: 50%;
  transform: rotate(-45deg);
  transform-origin: center;
  width: 0.625em; }
.orders {
  border: 1px solid #eeeeee;
  padding: 1.25rem;
  box-shadow: 0 0 0 transparent !important; }
.level-header {
  border-bottom: 1px solid #eeeeee;
  padding: 0 1.25rem 1.25rem;
  margin-left: -1.25rem;
  margin-right: -1.25rem; }
.wrapper-body {
  height: 300px;
  overflow-x: hidden; }
.media + .media {
  border-top: none;
  margin-top: .5rem;
  padding-top: .5rem; }
.chat-box {
  box-shadow: 0 0 0 transparent;
  position: relative;
  opacity: 1;
  background: #e0f3ff;
  border: 0;
  padding: 0.75rem 1.5rem;
  border-radius: 30px;
  border-top-left-radius: 0.25rem;
  flex: 1;
  display: flex;
  max-width: 50%;
  min-width: 100%;
  text-align: left; }
.subtitle:not(:last-child) {
  margin-bottom: 5px; }
